import React from 'react';
import ReactDOM from 'react-dom';
import './index.css'
class Hello extends React.Component {
    constructor(props) {
        super(props)
        this.state = { date: new Date() }
    }
    componentDidMount() {
        this.timeId = setInterval(() => this.tick(), 100)
    }

    componentWillUnmount() {
        clearInterval(this.timeId);
    }

    tick() {
        this.setState({
            date: new Date()
        })
    }

    render() {
        return <div>
            <h1>Hello {false}!</h1>
            <h2>{this.state.date.toLocaleTimeString()}</h2>
        </div>
    }
}

class Input extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            message: 'bin'
        }
        //  this.handleInput = this.handleInput.bind(this)
    }

    handleInput(e) {
        e.preventDefault();
        e.stopPropagation();
        this.setState({
            message: e.target.value
        })
    }
    render() {
        return <div>
            <input value={this.state.message} onChange={this.handleInput.bind(this)} />
            <h1 style={{ color: '#fff' }}>{this.state.message}</h1>
        </div>
    }
}

class Btn extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            toggle: true
        }
    }
    toggle1(e) {
        e.preventDefault();
        e.stopPropagation();
        this.setState({ toggle: false });
    }
    toggle2(e) {
        e.preventDefault();
        e.stopPropagation();
        this.setState({ toggle: true });
    }
    render() {
        let toggle = this.state.toggle;
        if (toggle) {
            return <div>
                <button onClick={this.toggle1.bind(this)}>Toggle1</button>
            </div>
        }
        else {
            return <div>
                <button onClick={this.toggle2.bind(this)}>Toggle2</button>
            </div>
        }
    }
}

class List extends React.Component {
    constructor(props) {
        super(props)
        this.state = { arr: ['', '', '', ''] }
    }

    render() {
        const ListArr = this.state.arr.map((val, index) => <li key={index}>{val}</li>)
        return <ul>{ListArr}</ul>
    }
}

class Select extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            val: 'first'
        }
    }

    handleChange(e) {
        this.setState({
            val: e.target.value
        })
    }

    render() {
        return <div>
            <select value={this.state.val} onChange={this.handleChange.bind(this)}>
                <option value="second"></option>
                <option value="first"></option>
                <option value="third"></option>
                <option value="forth"></option>
            </select>
            <span>{this.state.val}</span>
        </div>
    }
}

class InputTemprature extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            scaleNames: {
                t: 'Celsius',
                h: 'Fahrenheit'
            }
        }
    }

    handleChange(e) {
        this.props.onTemChange(e.target.value);
    }
    render() {
        const temprature = this.props.tem;
        const scal = this.props.type;
        return (
            <fieldset>
                <legend>{this.state.scaleNames[scal]}:</legend>
                <input value={temprature} onChange={this.handleChange.bind(this)} />
            </fieldset>
        );
    }
}
class InputGroup extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            scal: '',
            tem: ''
        }
    }

    changeTem(tem) {
        this.setState({ scal: 't', tem });
    }

    changeHer(tem) {
        this.setState({ scal: 'h', tem });
    }

    dealTem(t, fun) {
        let input = parseFloat(t);
        if (Number.isNaN(input)) {
            return '';
        }
        const output = fun(t);
        const rounded = Math.round(output * 1000) / 1000;
        return rounded.toString();
    }

    toTem(t) {
        return (t * 9 / 5) + 32;
    }

    toHer(t) {
        return (t - 32) * 5 / 9;
    }

    render() {
        const scal = this.state.scal;
        const temprature = this.state.tem;
        const tem1 = scal === "h" ? this.dealTem(temprature, this.toTem) : temprature;
        const her1 = scal === "t" ? this.dealTem(temprature, this.toHer) : temprature;
        return (<div>
            <InputTemprature tem={tem1} type="t" onTemChange={this.changeTem.bind(this)} />
            <InputTemprature tem={her1} type="h" onTemChange={this.changeHer.bind(this)} />
        </div>);
    }
}

class Slot extends React.Component {
    render() {
        return <div>
            <div style={{ color: 'red' }}>
                {this.props.first}
            </div>
            <div style={{ color: 'green' }}>
                {this.props.last}
            </div>
        </div>
    }
}

const data = [
    { category: "Sporting Goods", price: "$49.99", stocked: true, name: "Football" },
    { category: "Sporting Goods", price: "$9.99", stocked: true, name: "Baseball" },
    { category: "Sporting Goods", price: "$29.99", stocked: false, name: "Basketball" },
    { category: "Electronics", price: "$99.99", stocked: true, name: "iPod Touch" },
    { category: "Electronics", price: "$399.99", stocked: false, name: "iPhone 5" },
    { category: "Electronics", price: "$199.99", stocked: true, name: "Nexus 7" },
    { category: "people", name: "", stocked: true, price: "$2000000" },
    { category: "people", name: "", stocked: true, price: "$20" }
];

class Items extends React.Component {
    render() {
        return <li>
            <span style={{ display: "inline-block", width: "100px" }}>{this.props.name}</span>
            <span style={{ display: "inline-block", marginLeft: "50px" }}>{this.props.price}</span>
        </li>
    }
}

class Type extends React.Component {
    render() {
        let type = this.props.itemtype;
        let arr = [];
        for (let i = 0; i < this.props.itemdata.length; i++) {
            if (this.props.itemdata[i].category === type) {
                arr.push(<Items key={i} name={this.props.itemdata[i].name} price={this.props.itemdata[i].price}></Items>)
            }
        }
        return arr
    }
}

class CateLog extends React.Component {
    render() {
        let arr = [];
        let types = [];
        for (let i = 0; i < this.props.itemdata.length; i++) {
            if (arr.indexOf(this.props.itemdata[i].category) === -1) {
                types.push(<ul key={i} style={{ border: '1px solid red' }}><Type itemdata={this.props.itemdata} itemtype={this.props.itemdata[i].category} /></ul>);
                arr.push(this.props.itemdata[i].category);
            }
        }
        return types
    }
}

class UserInput extends React.Component {
    filter(e) {
        let val = e.target.value;
        this.props.onSearch(val)
    }

    render() {
        return <input type="text" onChange={this.filter.bind(this)} />
    }
}

class Box extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            OriginData: data,
            data: []
        }
    }

    componentDidMount() {
        let origin = this.state.OriginData;
        this.setState({ data: JSON.parse(JSON.stringify(origin)) })
    }

    search(val) {
        let data = this.state.OriginData;
        let arr = [];
        for (let i = 0; i < data.length; i++) {
            let exp = new RegExp(val, 'i')
            if (exp.test(data[i].name)) {
                arr.push(data[i])
            }
        }
        this.setState({ data: arr })
    }

    render() {
        if (this.state.data.length !== 0) {
            return <div style={{ border: 'solid 1px green', margin: '10px', padding: '20px' }}>
                <div>
                    <UserInput onSearch={this.search.bind(this)} />
                </div>
                <div style={{ marginTop: "20px" }}>
                    <span style={{ marginLeft: "32px" }}>name</span>
                    <span style={{ marginLeft: "110px" }}>price</span>
                    <CateLog itemdata={this.state.data} />
                </div>
            </div>
        } else {
            return <div style={{ border: 'solid 1px green', margin: '10px', padding: '20px' }}>
                <div>
                    <UserInput onSearch={this.search.bind(this)} />
                </div>
                <div style={{ marginTop: "20px" }}>
                    <span style={{ marginLeft: "32px" }}>name</span>
                    <span style={{ marginLeft: "110px" }}>price</span>
                    <div style={{ marginLeft: "32px" }}></div>
                </div>
            </div>
        }
    }
}

class RefP extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            val: ''
        }
    }

    con() {
        this.bin.value = 'bin';
        
    }

    render() {
        return <div>
            <input ref={(input) => { this.bin = input }} />
            <button onClick={this.con.bind(this)}></button>
        </div>
    }
}
var hello = <div><Hello name="Spark" /><Input /><Btn /><List /><Select /><InputGroup /><Box />
    <Slot first={<div></div>} last={<span></span>} /><RefP /></div>;

ReactDOM.render(
    hello,
    document.getElementById('root')
)



